<template>
  <div class="white">
        <v-alert type="info" icon="mdi-checkbox-multiple-marked-outline" border="left" class="my-0" tile>
          <h4 class="text-center">Digital Training and Internship for Youths in Ekiti State</h4>
        </v-alert>

   <v-row class="ma-1">
     <v-col cols="12" class="black--text text-center" >
    <b>Eligibility</b>
    <p class="mb-0"> <b>Age:</b> 18 to 35 years</p>
     <p class="mb-0"><b>Mininum Level of Education:</b> ND (National Diploma)</p> 
     <p><b>Duration:</b> 6 months</p>     
  </v-col >
    <v-col 
    cols="12" sm="6" md="3"
    v-for="item in items"
    :key="item.title"
    >
    <v-card
    hover
    :disabled="dform.program == item.title"
    class="transition-swing "
    >
    <v-img
    class="mx-auto"
    :src="`img/${item.src}`"
    max-height="300"
    max-width="300"
    contain
    >
      <template #placeholder>
        <v-skeleton-loader
          v-bind="attrs"
          type="card-avatar, image"
        ></v-skeleton-loader>
      </template>
    </v-img>
    <h4 class="primary--text mt-2 pl-3">{{item.title}}</h4>
    <p class="pa-2 text-justify">{{item.subtitle}}</p>
      <v-btn small :disabled="!!application_no || dform.program == item.title" @click="applyForm(item.title)" block class="info--text mt-2">APPLY</v-btn>
    </v-card>
    </v-col>
</v-row>
  
    <v-card v-if="showForm" tile class="px-2 mt-5">
      <h4 class="text-center mt-4 pt-4">YOU ARE APPLYING FOR <b class="info--text">{{dform.program}}</b></h4>
      <h5 class="text-center mb-2"><b class="info--text">All fields are required.</b></h5>
      <v-row justify="center">
        <v-col cols="10" sm="6" md="4" lg="3">
          <v-img
            :src="`/uploads/interns/${dform.passport}`"
            height="150"
            width="150"
            contain
            class="mx-auto"
          >
            <template #placeholder>
              <v-skeleton-loader
                v-bind="attrs"
                type="card-avatar, image"
              ></v-skeleton-loader>
            </template>
          </v-img>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="12" sm="7" md="5" lg="4">
          <v-file-input
            accept="image/*"
            label="Upload Passport (less than 75KB)"
            :rules="rules.img"
            @change="handlePassport"
            v-if="!dform.passport || edit.passport"
            autofocus
          ></v-file-input>
          <p v-else class="my-0 text-center">
            <v-btn
              small
              class="mx-auto"
              color="primary"
              @click="edit.passport = true"
              >Change Image</v-btn
            >
          </p>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="8" sm="4" md="3" lg="1">
          <p v-if="edit.passport" class="my-0 text-center">
            <v-btn
              small
              class="mx-auto"
              color="primary"
              @click="edit.passport = false"
              >Cancel Image Change</v-btn
            >
          </p>
        </v-col>
      </v-row>

      <v-row class="mt-2">
        <v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model="dform.surname"
            label="Surname:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
        <v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model="dform.firstname"
            label="First Name:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
        <v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model="dform.middlename"
            label="Middle Name:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
				<v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model.trim="dform.phoneno"
            maxlength="11"
            counter="11"
            label="Mobile Number:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
				<v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model.trim="dform.email"
            label="E-mail:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
        <v-col cols="12" md="6" lg="4">
          <v-select
            v-model="dform.gender"
            :items="genders"
            label="Gender:"
            outlined
            dense
            required
          >
          </v-select>
        </v-col>
        <v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model.trim="dform.dob"
            label="Date of Birth:"
            type="date"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
				<v-col cols="12" md="6" lg="4">
          <v-textarea
            v-model="dform.resident_address"
            label="Residential Address:"
            outlined
            dense
            auto-grow
            rows="1"
          >
          </v-textarea>
        </v-col>        
        <v-col cols="12" md="6" lg="4">
          <v-select
            v-model="dform.qualification"
            label="Highest Educational Qualification:"
            outlined
            dense
            :items="qualifications"
          >
          </v-select>
        </v-col>
        
        <v-col cols="12" md="6" lg="4">
          <v-text-field
            v-model="dform.discipline"
            label="Course of Study:"
            outlined
            dense
          >
          </v-text-field>
        </v-col>
        <v-col cols="12" md="6" lg="4">
          <v-select
            v-model="dform.religion"
            :items="religions"
            label="Religion:"
            outlined
            dense
            required
          >
          </v-select>
        </v-col>
        <v-col cols="12" md="6" lg="4">
          <v-radio-group v-model="dform.laptop" row>
            <span>Do you have a personal laptop?</span>
            <v-radio label="Yes" color="red" value="Yes" class="mx-2"></v-radio>
            <v-radio label="No" color="red" value="No" class="mx-2"></v-radio>            
          </v-radio-group>
        </v-col>				
        <v-col cols="12" md="6" lg="4">
          <v-radio-group v-model="dform.available" row>
            <span>Will you be available for the training: (Monday - Friday, 8am - 4pm)?</span>
            <v-radio label="Yes" color="red" value="Yes" class="mx-2"></v-radio>
            <v-radio label="No" color="red" value="No" class="mx-2"></v-radio>            
          </v-radio-group>
        </v-col>
				<v-col cols="12" md="6" lg="8">
          <v-file-input
            label="Upload CV (PDF/Word Format)"
            outlined
            append-icon="mdi-file-pdf-outline"
            dense
            accept=".pdf, .doc, .docx"
            :rules="rules.file"
            show-size
            v-if="!dform.cv|| edit.cv"
            @change="handleCV"
          >
          </v-file-input>
          <div class="text-center" v-else>
            <b>Curriculum Vitae: </b>
            <v-icon size="4rem">mdi-file-pdf-outline</v-icon>
            <v-btn
              color="primary"
              small
              shaped
              @click="edit.cv = true"
              class="mr-1"
              >change file</v-btn
            >
            <v-btn
              :href="`/uploads/interns/${dform.cv}`"
              download
              small
              shaped
              >download file</v-btn
            >
          </div>
          <div class="text-center" v-if="edit.cv">
            <v-btn color="primary" small shaped @click="edit.cv = false"
              >cancel change file</v-btn
            >
          </div>
        </v-col>				
      </v-row>
      <v-row justify="center" align="center">
      <v-btn
        shaped
        color="primary"
        @click="submitApplication"
        class="mb-5"
        :disabled="!valid"
        :loading="loading"
      >
        Submit Application
      </v-btn>
    </v-row>
    </v-card>
		<v-alert v-if="application_no" prominent type="success" border="left">
      <v-row align="center">
        <v-col class="grow">
          Application Submitted Successfully. Your application number is <b>{{ application_no }}</b>. Please keep your application number safe. You can click the close button.
        </v-col>
        <v-col class="shrink">
          <v-btn to="/" color="primary">close</v-btn>
        </v-col>
      </v-row>
    </v-alert>
    <v-alert type="info" icon="mdi-checkbox-multiple-marked-outline" border="left" class="my-0" tile>
      <h4 class="text-center"><b>Sponsor: Akintunde Oyebode</b></h4>
      </v-alert>
    <v-snackbar v-model="snackbar" :color="snackColor" bottom :timeout="5000">
      {{ snackMsg }}

      <template v-slot:action="{ attrs }">
        <v-btn color="dark" v-bind="attrs" @click="snackbar = false">
          <v-icon>mdi-close</v-icon>
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>

import { apiClient } from "@/services";
import { snackMixin } from "@/mixins";
import { mapState } from "vuex";
export default {
  name: "Interns",
  mixins: [snackMixin],
  data() {
    return {
      edit: { passport: false, cv: false },
      showForm:false,
      application_no: '',
      attrs: {
        class: "mb-6",
        boilerplate: true,
        elevation: 2,
      }, 
       items:[
                    { 
                      title:'SOFTWARE DEVELOPMENT', 
                      src:'softcode.jpg',
                      subtitle:'Immerse yourself in the core principles of software development and learn the necessary programming skills required for Web, Mobile or Desktop Application Development while working on projects.'
                    },
                    { 
                      title:'UI/UX AND GRAPHIC DESIGN', 
                      src:'ui.jpg',
                      subtitle:'Equip yourself with proper user interface, user experience, prototyping, wireframing and graphic design skills. Understanding different types of users and what they want, desirability and usability of a product, leveraging edge-cutting design tools.'                
                    },
                    {  
                      title:'VIDEO ANIMATIONS', 
                      src:'anime.jpg',
                      subtitle:'Learn the process of putting life into in-animate objects and turning them into illusionary live objects.'               
                    },
                    { 
                      title:'ELECTRICAL INSTALLATIONS', 
                      src:'install.jpg',
                      subtitle:'Learn necessary skills on electrical installations: home wiring, solar and inverter installation, CCTV installation for residential homes and offices.'                
                    }
                ],    
      dform: {
        id: "",
        application_no: "",
        surname: "",
        firstname: "",
        middlename: "",
        phoneno: "",
        email: "",
        gender: "",
        dob: "",
        resident_address: "",
        qualification: "",
        discipline: "",
        religion: "",
        laptop: "",
        available: "",
        program: "",
        passport: "",
        cv:"",
      },
      rules: {
        img: [
          (v) => !v || v.type.includes("image/") || "Upload an image file",
          (v) => !v || v.size < 78000 || "Image size should be less than 75KB"
        ],
        file: [
          (v) => !v || v.name.includes(".doc") ||  v.name.includes(".docx") ||  v.name.includes(".pdf") || "Upload a PDF/Word file"
        ],
      },
    };
  },
  computed: {
    ...mapState(["genders","religions", "qualifications", "programs"]),
    valid(){
      return this.dform.surname &&        
        this.dform.firstname &&
        this.dform.phoneno &&
        this.dform.email &&
        this.dform.gender &&
        this.dform.dob &&
        this.dform.resident_address &&
        this.dform.qualification &&
        this.dform.discipline &&
        this.dform.religion &&
        this.dform.laptop &&
        this.dform.available &&
        this.dform.program &&
        this.dform.passport &&
        this.dform.cv;
    }
  },
  methods: { 
    applyForm(title){
      this.dform.program = title;
      this.showForm && (this.showForm = false);
      this.$nextTick(() => this.showForm = true)
    },   
    submitApplication() {
      this.loading = true;
      // Do Validation here
      apiClient
        .post("/interns", this.dform)
        .then((res) => {
          // console.log(res.data);
          if (res.data.ok) {
            this.showForm = false;
            this.application_no = res.data.application_no;
            this.displayMsg("Application Submitted");
          } else {
            this.displayMsg(res.data, "error");
          }
        })
        .catch((err) => this.displayMsg(err.message, "error"));
    },
    handlePassport(file) {
      if (!file) {
        return;
      }
      const validImage = file.type.includes("image/") && file.size < 78000;
      if (!validImage) {
        this.displayMsg("Invalid Passport Image!", "error");
        return;
      }
      this.processFile(file, 'passport');
    },
    handleCV(file) {
      // console.log(file)
      if (!file) {
        return;
      }
      const validFile = file.name.includes(".doc") ||  file.name.includes(".docx") ||  file.name.includes(".pdf");
      if (!validFile) {
        this.displayMsg("Invalid CV file!", "error");
        return;
      }
      this.processFile(file, 'cv');          
    },
    processFile(file, filename){
      let fd = new FormData();
      let data = { filename: this.dform[filename] };
      fd.append("file", file);
      fd.append("data", JSON.stringify(data));
      let config = { header: { "Content-Type": "multipart/form-data" } };
      apiClient
        .post("/process_file", fd, config)
        .then((res) => {
          // console.log(res.data);
          if (res.data.filename) {
            this.dform[filename] = res.data.filename;
            this.edit[filename] = false;
            this.displayMsg("File uploaded successfully.");
          } else {
            this.displayMsg(res.data, "error");
          }
        })
        .catch((err) => this.displayMsg(err.message, "error"));
    },
    
  },
};
</script>
